export interface IDeepLinkOptions {
    appStoreUrl: string;
    appOpenerUrl: string;
}

export interface IDeepLink {
    appStoreUrl: string;
    appOpenerUrl: string;
    platform: "android" | "ios" | "unknown";
}


export class DeepLinkHelper {
    private static getMobileOS(): IDeepLink["platform"] {
        var userAgent = navigator.userAgent || navigator.vendor || (<any>window).opera;
        if (/android/i.test(userAgent)) {
            return "android";
        }
        if (/iPhone/.test(userAgent) && !(<any>window).MSStream) {
            return "ios";
        }
        return "unknown";
    }

    public static goToLink(options: Array<IDeepLink>) {
        const retVal = this.getMobileOS();
        const opt = options.find(i => i.platform === retVal);
        if (!opt) {
            throw new Error(`The Platform "${retVal}" is not implemented yet!`);
        }

        window.location.replace(opt.appOpenerUrl);
        setTimeout(() => {
            window.location.replace(opt.appStoreUrl);
        }, 8000);
    }
}
